import React, { useState, useEffect } from "react"
import { useLocation } from "@reach/router"
import { Link } from "gatsby"
import { Link as Scroll } from 'react-scroll'
import json from "/src/siteData.json"
import emailIcon from "/src/images/common/mail.png"
import HamburgerMenu from "/src/components/HamburgerMenu"

import instagram from "/src/images/icon/instagram.png"
import X from "/src/images/icon/x.png"
import LINE from "/src/images/icon/LINE.png"
import facebook from "/src/images/icon/facebook.png"
import youtube from "/src/images/icon/youtube.png"
import note from "/src/images/icon/note.png"
import tiktok from "/src/images/icon/tiktok.png"

import logo from "/src/images/common/logo.png"

const siteMetaData = json.siteMetadata
const logoUri = json.header.logoUri

const Component = () => {

  const location = useLocation();
  const isRecruitPage = location.pathname.includes("/recruit/");

  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 500) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const [menuIsOpen, setMenuIsOpen] = useState(false)
  return (
    <>
      <header id="gHeader" className={`${scrolled ? "on" : ""}`}>
        <h1 className="logo">
          <Link to="/">
            <img
              src={logo}
              alt={siteMetaData.title}
            />
          </Link>
        </h1>
        <div className="rBox">
          <ul className="snsLink">
            <li>
            <a href="https://www.instagram.com/notequalzero/" target="_blank" rel="noopener noreferrer">
              <img src={instagram} alt="instagram" />
            </a>
          </li>
          <li>
            <a href="https://x.com/NotEqual0" target="_blank" rel="noopener noreferrer">
              <img src={X} alt="X" />
            </a>
          </li>
          {/* <li>
            <a href="/" target="_blank" rel="noopener noreferrer">
              <img src={LINE} alt="LINE" />
            </a>
          </li> */}
          {/* <li>
            <a href="/" target="_blank" rel="noopener noreferrer">
              <img src={facebook} alt="facebook" />
            </a>
          </li> */}
          <li>
            <a href="https://www.youtube.com/@NotEqualZero" target="_blank" rel="noopener noreferrer">
              <img src={youtube} alt="youtube" />
            </a>
          </li>
          <li>
            <a href="https://note.com/notequalzero" target="_blank" rel="noopener noreferrer">
              <img src={note} alt="note" />
            </a>
          </li>
          <li>
            <a href="https://www.tiktok.com/@notequalzeroinc" target="_blank" rel="noopener noreferrer">
              <img src={tiktok} alt="tiktok" />
            </a>
            </li>
          </ul>
          <div className="btn">
            {isRecruitPage ?
              <Scroll to="form" smooth={true} duration={600} offset={-0}>
                <span className="info">
                  <span className="img">
                    <img
                      src={emailIcon}
                      alt=""
                    />
                  </span>
                  
                </span>
              </Scroll>
              :
              <Link to="https://web-notequalzero.com/index" target="_blank">
                <span className="info">
                  
                </span>
              </Link>
            }
          </div>
          <div
            className={`menu ${menuIsOpen ? "on" : ""}`}
            menuIsOpen={menuIsOpen}
            onClick={() => setMenuIsOpen(!menuIsOpen)}
          >
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>
        <ul className="sp_snsLink">
          <li>
            <a href="https://www.instagram.com/notequalzero/" target="_blank" rel="noopener noreferrer">
              <img src={instagram} alt="instagram" />
            </a>
          </li>
          <li>
            <a href="https://x.com/NotEqual0" target="_blank" rel="noopener noreferrer">
              <img src={X} alt="X" />
            </a>
          </li>
          {/* <li>
            <a href="/" target="_blank" rel="noopener noreferrer">
              <img src={LINE} alt="LINE" />
            </a>
          </li> */}
          {/* <li>
            <a href="/" target="_blank" rel="noopener noreferrer">
              <img src={facebook} alt="facebook" />
            </a>
          </li> */}
          <li>
            <a href="https://www.youtube.com/@NotEqualZero" target="_blank" rel="noopener noreferrer">
              <img src={youtube} alt="youtube" />
            </a>
          </li>
          <li>
            <a href="https://note.com/notequalzero" target="_blank" rel="noopener noreferrer">
              <img src={note} alt="note" />
            </a>
          </li>
          <li>
            <a href="https://www.tiktok.com/@notequalzeroinc" target="_blank" rel="noopener noreferrer">
              <img src={tiktok} alt="tiktok" />
            </a>
            </li>
        </ul>
      </header>

      <HamburgerMenu menuIsOpen={menuIsOpen} />
    </>
  )
}

export default Component
